@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --base-font-family: 'Noto Serif JP', '游明朝', 'Yu Mincho', YuMincho, 'Hiragino Mincho ProN', 'Hiragino Mincho Pro', serif;
  --second-font-family: 'Libre Franklin', sans-serif;
  --accent-font-family: 'Alex Brush', cursive;
  --padding-pc: 25px;
  --padding-sp: 20px;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
  --base-color: #2c3240;
  --accent-color: #bd4d51;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 1.0666666667vw;
  }
}
@media (min-width: 1500px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.inner {
  max-width: 90.625rem;
  padding: 0 var(--padding-pc);
  margin-inline: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: 37.5rem;
    padding: 0 var(--padding-sp);
  }
}

.header {
  position: absolute;
  top: 2.5rem;
  right: 2.5rem;
  z-index: 100;
}

.header__nav {
  background-color: var(--base-color);
  height: 3.5rem;
  display: flex;
  align-items: center;
  padding: 0 2.5rem;
}

.header__nav-list {
  display: flex;
  gap: 5rem;
}

.header__nav-item {
  /* リストアイテムのスタイル */
}

.header__nav-link {
  font-family: var(--base-font-family);
  font-size: 1rem;
  color: var(--white);
  line-height: 1.8;
  transition: opacity 0.3s ease;
}
@media (any-hover: hover) {
  .header__nav-link:hover {
    opacity: 0.7;
  }
}

.header__nav-item--active {
  position: relative;
}

.header__nav-item--active::after {
  content: "";
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 8.75rem;
  height: 0.125rem;
  background-color: var(--white);
}

.fv {
  position: relative;
  width: 100%;
  height: 89.625rem;
  background-color: #f5f5f5;
  overflow: hidden;
}

.fv__grid {
  position: absolute;
  top: 27.5rem;
  left: 34.75rem;
  width: 8.125rem;
  height: 8.5rem;
  z-index: 4;
}

.fv__grid-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__main-visual {
  position: absolute;
  top: -5.625rem;
  right: -11.5rem;
  width: 67.1875rem;
  height: 123.25rem;
  z-index: 3;
}

.fv__main-visual-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__bg {
  position: absolute;
  top: 43.3125rem;
  left: -1.1875rem;
  width: 121.875rem;
  height: 46.3125rem;
  z-index: 2;
}

.fv__bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__catch {
  position: absolute;
  top: 8.1875rem;
  left: 2.5rem;
  z-index: 10;
}

.fv__catch-title {
  display: flex;
  flex-direction: column;
}

.fv__catch-line {
  display: flex;
  align-items: baseline;
}

.fv__catch-line--1 {
  margin-bottom: -0.625rem;
}

.fv__catch-line--2 {
  margin-bottom: -0.3125rem;
}

.fv__catch-line--3 {
  margin-bottom: -0.3125rem;
}

.fv__catch-text {
  font-family: var(--second-font-family);
  font-size: 5.5375625rem;
  font-weight: var(--regular);
  line-height: 1;
}

.fv__catch-text--white {
  color: var(--white);
}

.fv__catch-text--dark {
  color: var(--base-color);
}

.fv__catch-text--accent {
  font-family: var(--accent-font-family);
  font-size: 7.3426875rem;
  color: var(--accent-color);
}

.fv__deco-line {
  position: absolute;
  background-color: var(--base-color);
  z-index: 3;
}

.fv__deco-line--1 {
  top: 9.4375rem;
  left: 2.5rem;
  width: 28.875rem;
  height: 4rem;
}

.fv__deco-line--2 {
  top: 29.625rem;
  left: 13.375rem;
  width: 16.4375rem;
  height: 4rem;
}

.fv__text-right {
  position: absolute;
  top: 19.375rem;
  left: 34.75rem;
  width: 20.0625rem;
  z-index: 4;
}

.fv__text-right-lead {
  font-family: var(--second-font-family);
  font-size: 0.875rem;
  color: var(--base-color);
  line-height: 1.8;
  text-align: center;
  margin-bottom: 1.25rem;
}

.fv__text-right-desc {
  font-family: var(--base-font-family);
  font-size: 0.8125rem;
  color: var(--base-color);
  line-height: 1.8;
  letter-spacing: 0.16em;
}

.fv__logo {
  display: block;
  width: 8.125rem;
  height: 8.5rem;
  margin: 1.875rem auto 0;
}

.fv__logo-img {
  width: 100%;
  height: 100%;
}

.fv__text-left {
  position: absolute;
  top: 50.1875rem;
  left: 22.125rem;
  width: 19.5625rem;
  z-index: 10;
}

.fv__text-left-desc {
  font-family: var(--base-font-family);
  font-size: 0.8125rem;
  color: var(--white);
  line-height: 2.12;
  letter-spacing: 0.11em;
}

.fv__thumbnail {
  position: absolute;
  z-index: 6;
}

.fv__thumbnail--1 {
  top: 45.875rem;
  left: 2.5rem;
  width: 17.1875rem;
  height: 21.5rem;
}

.fv__thumbnail--2 {
  top: 69.375rem;
  left: 22rem;
  width: 17.1875rem;
  height: 22.1875rem;
}

.fv__thumbnail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}